module.exports = "<h1 id=\"-\">使用條款</h1>\n<h2 id=\"-\">一、認知與接受條款</h2>\n<ol>\n<li>為保障您的權益，請您詳細閱讀本條款內容。確認您同意所有規範內容後，再開始註冊成為沃草有限公司（以下簡稱沃草）會員。如果您不同意本服務條款的內容，或者您所屬的國家或地域排除本服務條款內容之全部或部分時，您應立即停止使用本服務。此外，當您使用本服務之特定功能時，可能會依據該特定功能之性質，而須遵守本服務所另行公告之服務條款或相關規定。此另行公告之服務條款或相關規定亦均併入屬於本服務條款之一部分。沃草有權於任何時間修改或變更本服務條款之內容，並公告於本服務網站上，請您隨時注意該等修改或變更。若您於任何修改或變更後繼續使用本服務，則視為您已閱讀、了解並同意接受該等修改或變更。</li>\n<li>若您為未滿二十歲，除應符合上述規定外，並應於您的家長（或監護人）閱讀、瞭解並同意本約定書之所有內容及其後修改變更後，方得使用或繼續使用本服務。當您使用或繼續使用本服務，即表示您的家長（或監護人）已閱讀、瞭解並同意接受本約定書之所有內容及其後修改變更。</li>\n<li>會員及沃草雙方同意使用本服務之所有內容包括意思表示等，以電子文件作為表示方式。</li>\n<li>為了確保會員之個人資料、隱私及消費者權益之保護，於網站使用過程中將使用會員之個人資料，謹依個人資料保護法第8條規定告知以下事項：<ol>\n<li>蒐集之目的：蒐集之目的在於進行行銷業務、消費者、客戶管理與服務、網路購物及其他電子商務服務及與調查、統計與研究分析（法定特定目的項目編號為<code>〇四〇</code>、<code>〇九〇</code>、<code>一四八</code>、<code>一五七</code>）。沃草將藉由加入會員之過程或進行交易之過程來蒐集個人資料。</li>\n<li>蒐集之個人資料類別：沃草於網站內蒐集的個人資料包括，<ul>\n<li><code>C001</code> 辨識個人者：如會員之姓名、地址、電話、電子郵件等資訊。</li>\n<li><code>C002</code> 辨識財務者：如信用卡或金融機構帳戶資訊。</li>\n<li><code>C011</code> 個人描述：例如：性別、出生年月日、學歷等。</li>\n</ul>\n</li>\n<li>利用期間、地區、對象及方式：<ol>\n<li>期間：會員當事人要求停止使用或沃草停止提供服務之日為止。</li>\n<li>地區：會員之個人資料將使用於台灣地區。</li>\n<li>利用對象及方式：會員之個人資料蒐集除用於沃草之會員管理、客戶管理之檢索查詢等功能外，亦將利用於辨識身份、金流服務、物流服務、行銷廣宣等。例示如下：<ol>\n<li>以會員身份使用沃草提供之各項服務時，於頁面中自動顯示會員資訊。</li>\n<li>為遂行交易行為：會員對商品或勞務為預約、下標、購買、參與贈獎等之活動或從事其他交易時，關於商品配送、勞務提供、價金給付、回覆客戶之詢問、沃草對會員之詢問、相關售後服務及其他遂行交易所必要之業務。</li>\n<li>宣傳廣告或行銷等：提供會員各種電子雜誌等資訊、透過電子郵件、郵件、電話等提供與服務有關之資訊。將會員所瀏覽之內容或廣告，依客戶之個人屬性或購買紀錄、沃草網站之瀏覽紀錄等項目，進行個人化作業、會員使用服務之分析、新服務之開發或既有服務之改善等。針對民調、活動、留言版等之意見，或其他服務關連事項，與會員進行聯繫。</li>\n<li>回覆客戶之詢問：針對會員透過電子郵件、郵件、、傳真、電話或其他任何直接間接連絡方式向沃草所提出之詢問進行回覆。</li>\n<li>其他業務附隨之事項：附隨於上述<code>4.3.3.1</code>至<code>4.3.3.4</code>之利用目的而為沃草提供服務所必要之使用。</li>\n<li>對於各別服務提供者之資訊提供：會員對服務提供者之商品或勞務為預約、下標、購買、參加贈獎活動或申請其他交易時，沃草於該交易所必要之範圍內，得將會員之個人資料檔案提供予服務提供者，並由服務提供者負責管理該個人資料檔案。沃草將以規約課予服務提供者依保障會員隱私權之原則處理個人資料之義務，但無法保證服務提供者會必然遵守。詳細內容，請向各別服務提供者洽詢。</li>\n<li>其他：提供個別服務時，亦可能於上述規定之目的以外，利用個人資料。此時將在該個別服務之網頁載明其要旨。</li>\n</ol>\n</li>\n</ol>\n</li>\n<li>會員就個人資料之權利：沃草所蒐集個人資料之當事人，依個人資料保護法得對沃草行使以下權利：<ol>\n<li>查詢或請求閱覽。</li>\n<li>請求製給複製本。</li>\n<li>請求補充或更正。</li>\n<li>請求停止蒐集、處理或利用。</li>\n<li>請求刪除。</li>\n</ol>\n</li>\n<li>會員如欲行使上述權利，可與沃草客服連絡進行申請。</li>\n<li>請注意！如拒絕提供加入會員所需必要之資料，將可能導致無法享受完整服務或完全無法使用該項服務。</li>\n</ol>\n</li>\n</ol>\n<h2 id=\"-\">二、與第三人網站的連結</h2>\n<p>本服務或協力廠商可能會提供連結至其他網站或網路資源的連結。您可能會因此連結至其他業者經營的網站，但不表示沃草有限公司與該等業者有任何關係。其他業者經營的網站均由各該業者自行負責，不屬沃草有限公司控制及負責範圍之內。沃草有限公司對任何檢索結果或外部連結，不擔保其合適性、可依賴性、即時性、有效性、正確性及完整性。您也許會檢索或連結到一些令您厭惡或不需要的網站，這是網際網路運作的可能結果，遇到此類情形時，沃草有限公司建議您不要瀏覽或儘速離開該等網站。您並同意沃草有限公司無須為您連結至非屬於沃草有限公司之網站所生之任何損害，負損害賠償之責任。</p>\n<h2 id=\"-\">三、會員的註冊義務</h2>\n<p>為了能使用本服務，會員同意以下事項：</p>\n<ol>\n<li>依本服務註冊表之提示提供會員本人正確、最新的資料，且不得以第三人之名義註冊為會員。每位會員僅能註冊登錄一個帳號，不可重覆註冊登錄。</li>\n<li>即時維持並更新會員個人資料，確保其正確性，以獲取最佳之服務。</li>\n<li>若會員提供任何錯誤或不實的資料、或未按指示提供資料、或欠缺必要之資料、或有重覆註冊帳號等情事時，沃草有權不經事先通知，逕行暫停或終止會員的帳號，並拒絕會員使用本服務之全部或一部。</li>\n</ol>\n<h2 id=\"-\">四、沃草《隱私權政策》</h2>\n<ol>\n<li>為識別使用者與提供專案訂閱服務，本服務會蒐集使用者之電子信箱、居住地區、性別、生日、以及使用者於Facebook設定為公開之資訊。本服務會寄送平台電子報及使用者訂閱之專案更新至使用者登錄之信箱。本服務將就使用者資訊去個人化後進行數據統計與研究，以改善服務。本服務對於使用者、提案者或贊助者所取得之任何資訊，除上述使用範圍外，非經當事人同意、當事人自行公開或對行政、司法或他人法律程序之必要，否則本服務不會向任何其他對象分享您的個人資料。使用者可隨時在「帳號設定」中刪除或修改您登錄之一般使用者資料，或於接獲任何形式的聯絡時要求註銷所蒐集資料。</li>\n<li>本服務有權但無義務於使用者註銷資格屆滿五年後，刪除所蒐集之各項個人資料。</li>\n<li>《隱私權政策》內容，包括本網站如何處理在您使用網站服務時收集到的個人識別資料。《隱私權政策》不適用於本網站以外的相關連結網站，也不適用於非本網站所委託或參與管理的人員。</li>\n<li>沃草相關網站所取得的個人資料，都僅供沃草來於其內部、依照原來所說明的使用目的和範圍，除非事先說明、或依照相關法律規定，否則沃草不會將資料提供給第三人、或移作其他目的使用。本網站絕不會提供、交換、出租或出售任何您的個人資料給其他個人、團體、私人企業或公務機關，但有法律依據或合約義務者，不在此限。前項但書之情形包括不限於：<ol>\n<li>經由您書面同意。</li>\n<li>法律明文規定。</li>\n<li>為免除您生命、身體、自由或財產上之危險。</li>\n<li>與公務機關或學術研究機構合作，基於公共利益為統計或學術研究而有必要，且資料經過提供者處理或蒐集著依其揭露方式無從識別特定之當事人。</li>\n<li>當您在網站的行為，違反服務條款或可能損害或妨礙網站與其他使用者權益或導致任何人遭受損害時，經網站管理單位研析揭露您的個人資料是為了辨識、聯絡或採取法律行動所必要者。</li>\n<li>有利於您的權益。</li>\n<li>本網站委託廠商協助蒐集、處理或利用您的個人資料時，將對委外廠商或個人善盡監督管理之責。</li>\n</ol>\n</li>\n<li>為了提供您最佳的服務，本網站會在您的電腦中放置並取用我們的cookie，若您不願接受cookie的寫入，您可在您使用的瀏覽器功能項中設定隱私權等級為高，即可拒絕cookie的寫入，但可能會導至網站某些功能無法正常執行。</li>\n<li>本網站的《隱私權政策》將因應需求修正，但我們不會在未經您明確同意的情況下，即縮減本《隱私權政策》賦予您的權利。《隱私權政策》變更時一律會在本頁發佈；如果屬於重大變更，我們會提供更明顯的通知，包括某些服務會以電子郵件通知《隱私權政策》的變更。</li>\n</ol>\n<h2 id=\"-\">五、會員帳號、密碼及安全</h2>\n<ol>\n<li>完成本服務的登記程序之後，會員將取得一個特定之密碼及會員帳號，維持密碼及帳號之機密安全，是會員的責任。任何依照規定方法輸入會員帳號及密碼與登入資料一致時，無論是否由本人親自輸入，均將推定為會員本人所使用，利用該密碼及帳號所進行的一切行動，會員本人應負完全責任。</li>\n<li>會員同意以下事項：<ol>\n<li>會員的密碼或帳號遭到盜用或有其他任何安全問題發生時，會員將立即通知沃草。</li>\n<li>每次連線完畢，均結束會員的帳號使用。</li>\n<li>會員的帳號、密碼及會員權益均僅供會員個人使用及享有，不得轉借、轉讓他人或與他人合用。</li>\n<li>帳號及密碼遭盜用、不當使用或其他沃草無法辯識是否為本人親自使用之情況時，對此所致之損害，除證明係因可歸責於沃草之事由所致，沃草將不負任何責任。</li>\n<li>沃草若知悉會員之帳號密碼確係遭他人冒用時，將立即暫停該帳號之使用（含該帳號所生交易之處理）。</li>\n<li>當沃草同意使用Facebook帳號登入使用本服務時，該Facebook帳號之密碼是由Facebook網站所處理，沃草並不會接觸到該密碼，而且每一個Facebook帳號只能綁定一個沃草會員帳號進行登入，第一次綁定後即不能再予修改。</li>\n<li>就沃草而言，經由登入Facebook帳號及密碼而登入使用本服務時，該登入的帳號即代表會員本人，使用者於使用本服務之任何行為，均視為會員本人之行為，並需遵循沃草會員條款之各項約定。會員必須妥善設定、維護及保管自己之Facebook帳號及密碼，包含但不限使用本服務結束時應適時登出本服務之網站並同時登出Facebook帳號。</li>\n<li>如果會員洩漏自己的個人資料、密碼或付款資料，並使得第三人有使用的機會時，會員必須就第三人的行為負全部責任。沃草並可隨時暫停或終止提供本項使用Facebook帳號登入使用本服務之措施。</li>\n</ol>\n</li>\n</ol>\n<h2 id=\"-\">六、兒童及青少年之保護</h2>\n<p>為確保兒童及青少年使用網路的安全，並避免隱私權受到侵犯，家長（或監護人）應盡到下列義務：未滿十二歲之兒童使用本服務時時，應全程在旁陪伴，十二歲以上未滿十八歲之青少年使用本服務前亦應斟酌是否給予同意。</p>\n<h2 id=\"-\">七、使用者的守法義務及承諾</h2>\n<p>會員承諾絕不為任何非法目的或以任何非法方式使用本服務，並承諾遵守中華民國相關法規及一切使用網際網路之國際慣例。會員若係中華民國以外之使用者，並同意遵守所屬國家或地域之法令。會員同意並保證不得利用本服務從事侵害他人權益或違法之行為，包括但不限於：</p>\n<ol>\n<li>公布或傳送任何誹謗、侮辱、具威脅性、攻擊性、不雅、猥褻、不實、違反公共秩序或善良風俗或其他不法之文字、圖片或任何形式的檔案。</li>\n<li>侵害或毀損沃草或他人名譽、隱私權、營業秘密、商標權、著作權、專利權、其他智慧財產權及其他權利。</li>\n<li>違反依法律或契約所應負之保密義務。</li>\n<li>冒用他人名義使用本服務。</li>\n<li>傳輸或散佈電腦病毒。</li>\n<li>從事未經沃草事前授權的商業行為。</li>\n<li>刊載、傳輸、發送垃圾郵件、連鎖信、違法或未經沃草許可之多層次傳銷訊息及廣告等；或儲存任何侵害他人智慧財產權或違反法令之資料。</li>\n<li>對本服務其他用戶或第三人產生困擾、不悅或違反一般網路禮節致生反感之行為。</li>\n<li>其他不符本服務所提供的使用目的之行為或沃草有正當理由認為不適當之行為。</li>\n</ol>\n<h2 id=\"-\">八、服務內容之變更與電子報及商品訊息發送</h2>\n<ol>\n<li>會員同意沃草所提供本服務之範圍，沃草均得視業務需要及實際情形，增減、變更或終止相關服務的項目或內容，且無需個別通知會員。</li>\n<li>會員同意沃草得依實際執行情形，增加、修改或終止相關活動，並選擇最適方式告知會員。</li>\n<li>會員同意沃草得不定期發送電子報或商品訊息至會員所登錄的電子信箱帳號。當會員收到訊息後表示拒絕接受行銷時，沃草將停止繼續發送行銷訊息。</li>\n</ol>\n<h2 id=\"-\">九、服務之停止、中斷</h2>\n<p>沃草將依一般合理之技術及方式，維持系統及服務之正常運作。但於以下各項情況時，沃草有權可以停止、中斷提供本服務：</p>\n<ol>\n<li>沃草網站電子通信設備進行必要之保養及施工時。</li>\n<li>發生突發性之電子通信設備故障時。</li>\n<li>沃草網站申請之電子通信服務被停止，無法提供服務時。</li>\n<li>由於天災等不可抗力之因素或其他不可歸責於沃草致使沃草網站無法提供服務時。</li>\n</ol>\n<h2 id=\"-\">十、交易行為</h2>\n<ol>\n<li>會員使用本服務進行交易時，應依據沃草所提供之確認商品數量及價格機制進行。</li>\n<li>會員同意使用本服務訂購產品時，於沃草通知確認交易成立前，沃草仍保有不接受訂單或取消出貨之權利。會員向沃草發出訂購通知後，系統將自動發出接受通知，但此通知並非訂單確認通知，關於交易成立與否沃草將另行告知。若因訂單內容之標的商品或服務，其交易條件（包括但不限於規格、內容說明、圖片）有誤時，沃草仍得於下單後二工作日內拒絕該筆訂單。</li>\n<li>會員若於使用本服務訂購產品後倘任意退換貨、取消訂單、或有任何沃草認為不適當而造成沃草作業上之困擾或損害之行為，沃草將可視情況採取拒絕交易、暫停取消7-11或客樂得配送付款方式，或永久取消會員資格辦理。若會員訂購之產品若屬於以下情形：<ol>\n<li>預購類商品，</li>\n<li>商品頁顯示無庫存，</li>\n<li>須向供應商調貨，</li>\n<li>轉由廠商出貨，因商品交易特性之故，倘商品缺絕、或廠商因故無法順利供貨導致訂單無法成立時，沃草將以最適方式（以電子郵件為主，再輔以電話、郵遞或傳真等）告知。</li>\n</ol>\n</li>\n<li>會員使用本服務進行交易時，得依照消費者保護法之規定行使權利。因會員之交易行為而對本服務條款產生疑義時，應為有利於消費者之解釋。</li>\n</ol>\n<h2 id=\"-\">十一、廣告</h2>\n<p>您在本服務上瀏覽到的所有廣告內容、文字與圖片之說明、展示樣品或其他銷售資訊，均由各該廣告商、產品與服務的供應商所設計與提出。您對於廣告之正確性與可信度應自行斟酌與判斷。沃草有限公司僅接受委託予以刊登，不對前述廣告負擔保責任。</p>\n<h2 id=\"-\">十二、責任之限制與排除</h2>\n<ol>\n<li><p>本服務所提供之各項功能，均依該功能當時之現況提供使用，沃草對於其效能、速度、完整性、可靠性、安全性、正確性等，皆不負擔任何明示或默示之擔保責任。</p>\n</li>\n<li><p>沃草並不保證本服務之網頁、伺服器、網域等所傳送的電子郵件或其內容不會含有電腦病毒等有害物；亦不保證郵件、檔案或資料之傳輸儲存均正確無誤不會斷線和出錯等，因各該郵件、檔案或資料傳送或儲存失敗、遺失或錯誤等所致之損害，沃草不負賠償責任。</p>\n</li>\n</ol>\n<h2 id=\"-\">十三、智慧財產權的保護</h2>\n<ol>\n<li>沃草所使用之軟體或程式、網站上所有內容，包括但不限於著作、圖片、檔案、資訊、資料、網站架構、網站畫面的安排、網頁設計，均由沃草或其他權利人依法擁有其智慧財產權，包括但不限於商標權、專利權、著作權、營業秘密與專有技術等。任何人不得逕自使用、修改、重製、公開播送、改作、散布、發行、公開發表、進行還原工程、解編或反向組譯。若會員欲引用或轉載前述軟體、程式或網站內容，必須依法取得沃草或其他權利人的事前書面同意。尊重智慧財產權是會員應盡的義務，如有違反，會員應對沃草負損害賠償責任（包括但不限於訴訟費用及律師費用等）。</li>\n<li>在尊重他人智慧財產權之原則下，會員同意在使用沃草之服務時，不作侵害他人智慧財產權之行為。</li>\n<li>若會員有涉及侵權之情事，沃草可暫停全部或部份之服務，或逕自以取消會員帳號之方式處理。</li>\n<li>若有發現智慧財產權遭侵害之情事，請將所遭侵權之情形及聯絡方式，並附具真實陳述及擁有合法智慧財產權之聲明，以下列方式聯絡沃草：<ol>\n<li>以電子郵件（Email）寄送至<code>info@watchout.tw</code>。</li>\n</ol>\n</li>\n</ol>\n<h2 id=\"-\">十四、會員之行為</h2>\n<ol>\n<li><p>由會員公開張貼或私下傳送的資訊、資料、文字、軟體、音樂、音訊、照片、圖形、視訊、信息或其他資料（以下簡稱「會員內容」），均由會員內容提供者自負責任。沃草有限公司無法控制經由本服務而張貼之會員內容，因此不保証其正確性、完整性或品質。您了解使用本服務時，可能會接觸到令人不快、不適當、令人厭惡之會員內容。在任何情況下，沃草有限公司均不為任何會員內容負責，包含但不限於任何錯誤或遺漏，以及經由本服務張貼、發送電子郵件或傳送而衍生之任何損失或損害。</p>\n</li>\n<li><p>您了解沃草有限公司並未針對會員內容事先加以審查，但沃草有限公司有權（但無義務）依其自行之考量，拒絕或移除經由本服務提供之任何會員內容。在不限制前開規定之前提下，沃草有限公司及其指定人有權將違反本服務條款和令人厭惡之任何會員內容加以移除。您使用任何會員內容時，就前開會員內容之正確性、完整性或實用性之情形，您同意必須自行加以評估並承擔所有風險。您了解並同意，沃草有限公司依據法律的要求，或基於以下目的之合理必要範圍內，認定必須將會員內容加以保存或揭露予政府機關、司法警察或未成年人之監護人時，得加以保存及揭露：</p>\n<ol>\n<li>遵守法令或政府機關之要求律程序。</li>\n<li>執行本服務條款。</li>\n<li>回應任何侵害第三人權利之主張。</li>\n<li>保護沃草有限公司及其使用者及公眾之權利、財產或個人安全。</li>\n</ol>\n</li>\n</ol>\n<h2 id=\"-\">十五、會員對沃草之授權</h2>\n<p>對於會員上載、傳送、輸入或提供之資料，會員同意沃草網站得於合理之範圍內蒐集、處理、保存、傳遞及使用該等資料，以提供使用者其他資訊或服務、或作成會員統計資料、或進行關於網路行為之調查或研究，或為任何之合法使用。若會員無合法權利得授權他人使用、修改、重製、公開播送、改作、散布、發行、公開發表某資料，並將前述權利轉授權第三人，請勿擅自將該資料上載、傳送、輸入或提供至沃草。任何資料一經會員上載、傳送、輸入或提供至沃草時，視為會員已允許沃草無條件使用、修改、重製、公開播送、改作、散布、發行、公開發表該等資料，並得將前述權利轉授權他人，會員對此絕無異議。會員並應保證沃草使用、修改、重製、公開播送、改作、散布、發行、公開發表、轉授權該等資料，不致侵害任何第三人之智慧財產權，否則應對沃草負損害賠償責任（包括但不限於訴訟費用及律師費用等）。</p>\n<h2 id=\"-\">十六、特別授權事項</h2>\n<p>因使用本服務所提供之網路交易或活動，可能須透過宅配或貨運業者始能完成貨品（或贈品等）之配送或取回，因此，會員同意並授權沃草得視該次網路交易或活動之需求及目的，將由會員所提供且為配送所必要之個人資料（如收件人姓名、配送地址、連絡電話等），提供予宅配貨運業者及相關配合之廠商，以利完成該次貨品（或贈品等）之配送、取回。</p>\n<h2 id=\"-\">十七、拒絕或終止會員的使用</h2>\n<p>會員同意沃草得基於維護交易安全之考量，因任何理由，包含但不限於缺乏使用，或違反本服務條款的明文規定及精神，終止會員的密碼、帳號（或其任何部分）或本服務（或其任何部分）之使用，或將本服務內任何「會員內容」加以移除並刪除。此外，會員同意若本服務（或其任何部分）之使用被終止，沃草對會員或任何第三人均不承擔責任。</p>\n<h2 id=\"-\">十八、準據法與管轄法院</h2>\n<p>本服務條款之解釋與適用，以及與本服務條款有關或會員與沃草間因交易行為而產生之爭議或糾紛，應依照中華民國法律予以處理，並以台灣台北地方法院為第一審管轄法院，但若法律對於管轄法院另有強制規定者，仍應依其規定。</p>\n";

